<template>
  <div class="ads">
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Ads</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('search')"> &#xe625; </a>
      </div>
    </section>

    <div class="tabWrap">
      <van-tabs title-active-color="#f44" @click="onClick">
        <van-tab v-for="(item, index) in types" :key="index" :title="item.name">
          <vx-pull-refresh @refresh="onRefresh">
            <van-list
              v-model="loading"
              :finished="finished"
              finished-text="The end"
              @load="onLoad"
            >
              <ul>
                <li v-for="(ad, i) in list" :key="i">
                  <div class="adsWrap">
                    <div class="userheadWrap">
                      <div class="userHead">
                        <img :src="ad.headImage" />
                      </div>
                      <span>{{ ad.name }}</span>
                    </div>
                    <div class="connect">
                      {{ ad.content }}
                      <a v-if="false">{{ ad.rushIn }}</a>
                    </div>
                    <div class="content">
                      <p v-if="false">See More</p>
                      <div class="photoWrap">
                        <div class="photo_top">
                          <div
                            class="photo_left"
                            v-if="ad.imgList && ad.imgList.length > 0"
                          >
                            <img :src="ad.imgList[0].img" />
                          </div>
                          <div
                            class="photo_right"
                            v-if="ad.imgList && ad.imgList.length > 1"
                          >
                            <img :src="ad.imgList[1].img" />
                          </div>
                        </div>
                        <div class="photo_btm">
                          <div
                            class="photoList mr9"
                            v-if="ad.imgList && ad.imgList.length > 2"
                          >
                            <img :src="ad.imgList[2].img" />
                          </div>
                          <div
                            class="photoList mr9"
                            v-if="ad.imgList && ad.imgList.length > 3"
                          >
                            <img :src="ad.imgList[3].img" />
                          </div>
                          <div
                            class="photoList mr9"
                            v-if="ad.imgList && ad.imgList.length > 4"
                          >
                            <img :src="ad.imgList[4].img" />
                          </div>
                          <!-- <div class="tail">
                                            <span>+6</span>
                                        </div> -->
                        </div>
                      </div>
                    </div>
                    <div class="downTime">
                      <span class="reverse">Count Down</span>
                      <span class="finish">FinishTime</span>
                    </div>
                    <div class="dayTime">
                      <span class="reverse"><span>02</span> Days</span>
                      <span class="finish"> {{ StringToDate(ad.endTime) }}</span>
                    </div>
                    <div class="btmWrap">
                      <van-button type="danger" @click="$router.push(ad.rushIn)"
                        >Rush In</van-button
                      >
                      <van-button
                        type="danger"
                        @click="
                          $router.push({
                            path: 'sharingModel',
                            query: { id: ad.id }
                          })
                        "
                        >Share</van-button
                      >
                    </div>
                  </div>
                </li>
              </ul>
            </van-list>
          </vx-pull-refresh>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import {
  getAllPromotionalMaterialByType,
  getAllPromotionalMaterialType
} from "@/api/flashSale";
export default {
  name: "ads",
  data() {
    return {
      value: "",
      active: 0,
      types: [],
      selectType: 0,
      list: {},
      loading: false,
      finished: false,
      isLoading: true,
      query: {}
    };
  },
  computed: {},
  mounted() {
        getAllPromotionalMaterialType().then(res => {
        this.types = res.data.result;
        this.selectType = this.types[0].type;
    this.init();
        });

  },
  methods: {
    init() {
    this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });

        getAllPromotionalMaterialByType({
          type: this.selectType,
          pageNo: this.query.pageNo,
          pageSize: this.query.pageSize,
        }).then(res => {
          this.list = res.data.result.result;
        });
    },
     onLoad() {
       if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.query.pageNo++;
        getAllPromotionalMaterialByType({
          type: this.selectType,
          pageNo: this.query.pageNo,
          pageSize: this.query.pageSize,
        })
        .then(res => {
          this.list=this.list.concat(res.data.result.result);
           this.loading = false;
          if (res.data.result.totalNum <= this.list.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading=false;
        });
    },
    onRefresh(done) {
      setTimeout(() => {
        this.init();
        done()
      }, 500);

    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    onClick(index) {
      this.selectType = this.types[index].type;
      this.init();
    },
    StringToDate(DateStr)  {   
      var converted = Date.parse(DateStr);  
      var myDate = new Date(converted);  
      if (isNaN(myDate))  
        {   
            //var delimCahar = DateStr.indexOf('/')!=-1?'/':'-';  
            var arys= DateStr.split('-');  
            myDate = new Date(arys[0],--arys[1],arys[2]);  
        }  
        var now = myDate;
        var year = now.getFullYear();       //年
        var month = now.getMonth() + 1;     //月
        var day = now.getDate();            //日
       
        var hh = now.getHours();            //时
        var mm = now.getMinutes();          //分
        var ss = now.getSeconds();          //秒

        if(day < 10)
            clock += "0";
           
        var clock = day + "/";

        if(month < 10)
            clock += "0";
       
        clock += month + "/";

        clock += year + " ";
       
       
        if(hh < 10)
            clock += "0";
           
        clock += hh + ":";

        if (mm < 10)
          clock += '0'; 

        clock += mm + ":";
 
        if (ss < 10)
          clock += '0';

        clock += ss;
        return(clock); 

    } 
  }
};
</script>

<style lang="scss" scoped>
  .ads{
  .tabWrap {
  .van-tab {
    flex-basis: 0 !important;
    min-width: auto;
    padding: 0 15px;
  }
  li {
    border-top: 10px solid #f6f6f6;
  }
  }

  .adsWrap {
    padding: 0 4%;
  }
  .userheadWrap {
    width: 100%;

    display: flex;
    align-items: center;
    padding-top: 10px;
  }
  .userHead {
    padding-right: 10px;
  img {
    width: 40px;
  }
  }
  .connect {
    width: 100%;
    word-break: break-all;
    word-wrap: break-word;
    font-size: 14px;
  a {
    color: #2b87d5;
  }
  }
  .content {
  p {
    color: #2b87d5;
    padding: 2px 0 10px 0;
    font-size: 13px;
  }
  }
  .photo_top {
    display: -webkit-box;
  }
  .photo_left {
    width: 166px;
    height: 102px;
    margin-right: 12px;
  img {
    width: 100%;
    height: 100%;
  }
  }
  .photo_right {
    width: 166px;
    height: 102px;
  img {
    width: 100%;
    height: 100%;
  }
  }
  .photo_btm {
    display: -webkit-inline-box;
    margin-top: 10px;
  }
  .photoList {
    width: 109px;
    height: 106px;

  img {
    width: 100%;
    height: 100%;
  }
  }
  .mr9 {
    margin-right: 9px;
  }
  .tail {
    width: 109px;
    height: 106px;
    background: url(../common/images/photo.png) no-repeat;
    background-size: 100% 100%;
    opacity: 0.5;
  span {
    color: #fff;
    font-size: 40px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  }
  .downTime {
    width: 100%;
    display: -webkit-box;
    padding-top: 7px;
    font-size: 10px;
  span {
    display: block;
    width: 50%;
    color: #e33946;
  }
  .finish {
    color: #999999;
    display: flex;
    justify-content: center;
    padding-left: 9px;
  }
  }
  .dayTime {
    width: 100%;
    display: -webkit-box;
    padding-top: 5px;
    font-size: 12px;
  .reverse {
    display: -webkit-box;
    width: 50%;
    color: #e33946;
  span {
    background-color: #e33946;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 19px;
    width: 19px;
    border-radius: 3px;
    margin-right: 5px;
  }
  }
  .finish {
    width: 50%;
    color: #999999;
    display: flex;
    justify-content: flex-end;
  }
  }
  .van-button--danger {
    height: 28px;
    line-height: 27px;
    padding: 0;
    width: 81px;
    font-size: 15px;
  }
  .van-button--danger:first-child {
    margin-right: 8px;
    background-color: #b20808;
    border-color:#b20808;
  }

  .btmWrap {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 17px;
    padding-top: 1px;
  }
  }

</style>



